/* Import Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;700;900&family=Space+Grotesk:wght@300;400;500;600;700&display=swap');

/* Reset and Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.unauthorized-container {
  min-height: 100vh;
  width: 100%;
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Space Grotesk', sans-serif;
}

/* Animated Background */
.background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #0c0c0c 0%, #001031 25%, #0d1325 50%, #0a3001f5 75%, #072700 100%);
  z-index: 1;
}

.floating-shapes {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.shape {
  position: absolute;
  border-radius: 50%;
  background: linear-gradient(45deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.05));
  animation: float 20s infinite linear;
}

.shape-1 {
  width: 80px;
  height: 80px;
  top: 20%;
  left: 10%;
  animation-delay: 0s;
  animation-duration: 25s;
}

.shape-2 {
  width: 120px;
  height: 120px;
  top: 60%;
  right: 15%;
  animation-delay: -5s;
  animation-duration: 30s;
}

.shape-3 {
  width: 60px;
  height: 60px;
  top: 80%;
  left: 20%;
  animation-delay: -10s;
  animation-duration: 20s;
}

.shape-4 {
  width: 100px;
  height: 100px;
  top: 10%;
  right: 30%;
  animation-delay: -15s;
  animation-duration: 35s;
}

.shape-5 {
  width: 40px;
  height: 40px;
  top: 40%;
  left: 5%;
  animation-delay: -20s;
  animation-duration: 15s;
}

@keyframes float {
  0% {
    transform: translateY(100vh) rotate(0deg);
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    transform: translateY(-100px) rotate(360deg);
    opacity: 0;
  }
}

.gradient-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 30% 20%, rgba(120, 119, 198, 0.3) 0%, transparent 50%),
              radial-gradient(circle at 70% 80%, rgba(255, 119, 198, 0.2) 0%, transparent 50%);
}

/* Main Content */
.content {
  position: relative;
  z-index: 10;
  text-align: center;
  color: white;
  max-width: 800px;
  padding: 2rem;
}

/* 403 Number */
.error-number {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
  font-family: 'Orbitron', monospace;
  font-weight: 900;
}

.four {
  font-size: clamp(8rem, 15vw, 12rem);
  background: linear-gradient(45deg, #ff6b6b, #4ecdc4, #45b7d1, #96ceb4);
  background-size: 400% 400%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: gradientShift 3s ease-in-out infinite;
  text-shadow: 0 0 30px rgba(255, 255, 255, 0.5);
}

.three {
  font-size: clamp(8rem, 15vw, 12rem);
  background: linear-gradient(45deg, #ff6b6b, #4ecdc4, #45b7d1, #96ceb4);
  background-size: 400% 400%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: gradientShift 3s ease-in-out infinite;
  text-shadow: 0 0 30px rgba(255, 255, 255, 0.5);
}

.zero {
  position: relative;
  width: clamp(8rem, 15vw, 12rem);
  height: clamp(8rem, 15vw, 12rem);
  margin: 0 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.zero-inner {
  width: 100%;
  height: 100%;
  border: 8px solid transparent;
  border-radius: 50%;
  background: linear-gradient(45deg, #ff6b6b, #4ecdc4, #45b7d1, #96ceb4);
  background-size: 400% 400%;
  background-clip: padding-box;
  animation: gradientShift 3s ease-in-out infinite, rotate 10s linear infinite;
  position: relative;
  overflow: hidden;
}

.lock-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60%;
  height: 60%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.lock-body {
  width: 40px;
  height: 30px;
  background: linear-gradient(135deg, #e8e8e8, #f5f5f5);
  border-radius: 8px 8px 0 0;
  position: relative;
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.1);
}

.lock-shackle {
  width: 20px;
  height: 20px;
  border: 4px solid #e8e8e8;
  border-radius: 50%;
  position: absolute;
  top: -10px;
  left: 50%;
  transform: translateX(-50%);
  border-bottom: none;
  border-right: none;
  animation: lockPulse 2s ease-in-out infinite;
}

@keyframes gradientShift {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes lockPulse {
  0%, 100% {
    transform: translateX(-50%) scale(1);
    opacity: 1;
  }
  50% {
    transform: translateX(-50%) scale(1.1);
    opacity: 0.8;
  }
}

/* Error Message */
.error-message {
  margin-bottom: 3rem;
}

.error-title {
  font-size: clamp(2rem, 5vw, 3.5rem);
  font-weight: 700;
  margin-bottom: 1rem;
  background: linear-gradient(45deg, #ffffff, #e0e0e0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.error-description {
  font-size: clamp(1rem, 2.5vw, 1.2rem);
  line-height: 1.6;
  color: rgba(255, 255, 255, 0.8);
  max-width: 600px;
  margin: 0 auto;
}

/* Action Buttons */
.action-buttons {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 3rem;
}

.btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 1rem 2rem;
  border: none;
  border-radius: 50px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  position: relative;
  overflow: hidden;
}

.btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.btn:hover::before {
  left: 100%;
}

.btn-primary {
  background: linear-gradient(45deg, #158638, #09365F);
  color: white;
  box-shadow: 0 4px 15px rgba(21, 134, 56, 0.4);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(21, 134, 56, 0.6);
}

.btn-secondary {
  background: transparent;
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.3);
}

.btn-secondary:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.6);
  transform: translateY(-2px);
}

/* Security Elements */
.security-elements {
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.shield {
  position: absolute;
  animation: shieldFloat 15s linear infinite;
}

.shield-1 {
  width: 60px;
  height: 60px;
  background: linear-gradient(45deg, #158638, #47AD94);
  clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
  top: 20%;
  right: 10%;
  animation-delay: 0s;
  box-shadow: 0 0 20px rgba(21, 134, 56, 0.5);
}

.shield-2 {
  width: 40px;
  height: 40px;
  background: linear-gradient(45deg, #09365F, #3289EE);
  clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
  bottom: 30%;
  left: 15%;
  animation-delay: -7s;
  box-shadow: 0 0 15px rgba(9, 54, 95, 0.5);
}

@keyframes shieldFloat {
  0% {
    transform: rotate(0deg) translateX(0) translateY(0);
  }
  25% {
    transform: rotate(90deg) translateX(50px) translateY(-30px);
  }
  50% {
    transform: rotate(180deg) translateX(0) translateY(0);
  }
  75% {
    transform: rotate(270deg) translateX(-50px) translateY(30px);
  }
  100% {
    transform: rotate(360deg) translateX(0) translateY(0);
  }
}

.key {
  position: absolute;
  top: 15%;
  left: 20%;
  animation: keyMove 12s linear infinite;
}

.key-body {
  width: 30px;
  height: 15px;
  background: linear-gradient(45deg, #043DFF, #3289EE);
  border-radius: 3px;
  position: relative;
  box-shadow: 0 0 10px rgba(4, 61, 255, 0.5);
}

.key-teeth {
  width: 8px;
  height: 8px;
  background: linear-gradient(45deg, #043DFF, #3289EE);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  right: -4px;
  transform: translateY(-50%);
}

@keyframes keyMove {
  0% {
    transform: translateX(0) translateY(0) rotate(0deg);
  }
  25% {
    transform: translateX(100px) translateY(-50px) rotate(90deg);
  }
  50% {
    transform: translateX(200px) translateY(0) rotate(180deg);
  }
  75% {
    transform: translateX(100px) translateY(50px) rotate(270deg);
  }
  100% {
    transform: translateX(0) translateY(0) rotate(360deg);
  }
}

/* Floating Particles */
.particles {
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.particle {
  position: absolute;
  width: 2px;
  height: 2px;
  background: white;
  border-radius: 50%;
  animation: particleFloat 8s linear infinite;
  opacity: 0.6;
}

.particle:nth-child(odd) {
  animation-delay: -2s;
  animation-duration: 10s;
}

.particle:nth-child(even) {
  animation-delay: -4s;
  animation-duration: 12s;
}

@keyframes particleFloat {
  0% {
    transform: translateY(100vh) translateX(0);
    opacity: 0;
  }
  10% {
    opacity: 0.6;
  }
  90% {
    opacity: 0.6;
  }
  100% {
    transform: translateY(-100px) translateX(100px);
    opacity: 0;
  }
}

/* RTL Support */
.unauthorized-container.rtl {
  direction: rtl;
  text-align: right;
}

.unauthorized-container.rtl .content {
  text-align: center;
}

.unauthorized-container.rtl .error-number {
  flex-direction: row-reverse;
}

.unauthorized-container.rtl .action-buttons {
  flex-direction: row-reverse;
}

.unauthorized-container.rtl .btn {
  flex-direction: row-reverse;
}

.unauthorized-container.rtl .btn i {
  margin-left: 0.5rem;
  margin-right: 0;
}

/* Responsive Design */
@media (max-width: 768px) {
  .content {
    padding: 1rem;
  }
  
  .action-buttons {
    flex-direction: column;
    align-items: center;
  }
  
  .btn {
    width: 100%;
    max-width: 300px;
  }
  
  .security-elements {
    display: none;
  }
  
  .unauthorized-container.rtl .action-buttons {
    flex-direction: column;
  }
  
  .unauthorized-container.rtl .btn {
    flex-direction: row;
  }
  
  .unauthorized-container.rtl .btn i {
    margin-left: 0;
    margin-right: 0.5rem;
  }
}

@media (max-width: 480px) {
  .error-number {
    flex-direction: column;
    gap: 1rem;
  }
  
  .zero {
    margin: 0;
  }
  
  .unauthorized-container.rtl .error-number {
    flex-direction: column;
  }
}
